body {
  font-family: 'Arial', sans-serif;
  background-color: #f5f5f5;
}

.container {
  max-width: 600px;
  margin: 50px auto;
  background-color: #fff;
  padding: 20px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}

h1 {
  font-size: 28px;
  color: #333;
  text-align: center;
}

form {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}

input {
  flex-grow: 1;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

button {
  margin-left: 10px;
  padding: 8px 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

h2 {
  font-size: 22px;
  color: #333;
  margin-bottom: 10px;
}

ul {
  padding: 0;
  list-style-type: none;
}

li {
  padding: 5px 10px;
  border-bottom: 1px solid #eee;
}

li:last-child {
  border-bottom: none;
}

